@import url('fonts.css');

.TEMP_DEV_HOME_BUTTON {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
}

* {
    box-sizing: border-box;
}

html {
    font-size: 16px;
}

body {
    font-family: Rubik, -apple-system, Roboto, Droid Sans, Ubuntu, Arial, Helvetica Neue, Helvetica, sans-serif;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.game__outer {
    display: none;
    position: relative;
    justify-content: center;
    box-sizing: border-box;
}
.game__outer.adjusted {
    display: block;
}

.game__inner {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: scroll;
}

/*
 * DEMO STYLES
 */

html {
    background: magenta;
}

body {
    background: #333;
    background: radial-gradient(ellipse at center,  #333 0%,#333 50%,#000 100%);
    background-size: 5px 5px;
}

.game__outer {
    background: #333;
    border: 1px solid #fff;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.25);
}

.game__inner {
    border: 2px solid #000;
    background-color: #fff;
}






