.game {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.game__title {
    text-align: center;

    font-size: 4rem;
    font-family: 'Lilita One';

    color: #fff;

    text-shadow: -0.1rem -0.1rem 0 blue,
    0.2rem 0.2rem 0 blue,
    0.4rem 0.4rem 0 lightblue;

    letter-spacing: 0.4rem;
    position: relative;
    margin: 0 auto;
    width: 18rem;
    height: 18rem;



    @media (orientation: landscape) {
        & {
            margin: 0;
        }
    }
}

.game__title--clash,
.game__title--of,
.game__title--emoji,
.game__title--alpha,
.game__title--poo,
.game__title--bomb {
    position: absolute;
}

.game__title--clash {
    font-size: 3.5rem;
    top: 3.5rem;
    left: 1rem;
    transform: rotate(-15deg);
}
.game__title--of {
    font-size: 2rem;
    top: 6rem;
    right: 7.5rem;
}
.game__title--emoji {
    top: 7rem;
    right: 2rem;
}
.game__title--alpha {
    font-size: 2rem;
    bottom: 3.5rem;
    right: 0.5rem;
    font-style: normal;
    text-shadow:
        0.1rem  0.1rem 0 #000,
        -0.1rem  0.1rem 0 #000,
        0.1rem -0.1rem 0 #000,
        -0.1rem -0.1rem 0 #000,
        0.3rem 0.3rem 0 rgba(0, 0, 0, 0.6);
}
.game__title--poo {
    font-size: 3.5rem;
    top: 7.5rem;
    left: 1.6rem;
    text-shadow:
        0.1rem  0.1rem 0 #000,
        -0.1rem  0.1rem 0 #000,
        0.1rem -0.1rem 0 #000,
        -0.1rem -0.1rem 0 #000,
        0.3rem 0.3rem 0 rgba(0, 0, 0, 0.6);
}
.game__title--bomb {
    font-size: 3.1rem;
    top: 7.8rem;
    right: 3.8rem;
    text-shadow:
         0.1rem  0.1rem 0 #000,
        -0.1rem  0.1rem 0 #000,
         0.1rem -0.1rem 0 #000,
        -0.1rem -0.1rem 0 #000,
        0.3rem 0.3rem 0 rgba(0, 0, 0, 0.6);
}

$nr: 20; // number of rays
$p: 100%/$nr; // percent of circle allocated to a ray and gap after
$m: repeating-conic-gradient(#000 0% .5*$p, transparent 0% $p);
$rays_width: 100rem;
$rays_height: 70rem;
@keyframes infinite-spinning {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
.game__title--background {
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    margin-top: -$rays_height/3;
    margin-left: -$rays_width/2;
    width: $rays_width;
    height: $rays_height;
    background: linear-gradient(rgba(255, 255, 255, 0.1), #2190FF);
    mask: $m;
    animation: linear infinite-spinning 140s infinite none;
}
.game__title--buttons {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
}


.game__header {
    background: #ddd;
    flex-grow: 0;
    position: relative;
    z-index: 1;
}
.topBar__default {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.topBar__button {

}
.topBar__resources--item {}

.game__body {
    flex-grow: 1;
    overflow: auto;
    min-height: 0;
    background: #fff;
}
.game__footer {
    background: #ddd;
    flex-grow: 0;
    position: relative;
    z-index: 1;
    text-align: right;
    padding: 0.3rem;
}




.buttonLink--wrapper {
    text-decoration: none;
}

.button--base {
    display: block;
    margin: 1rem auto;
    padding: 1rem;
    border: 0.2rem solid black;
    background: darkgray;
    color: #fff;
    font-size: 1.2rem;
    font-weight: bold;
    text-shadow:
        0.1rem  0.1rem 0 #000,
        -0.1rem  0.1rem 0 #000,
        0.1rem -0.1rem 0 #000,
        -0.1rem -0.1rem 0 #000,
        0.175rem 0.175rem 0 rgba(0, 0, 0, 0.6);
    box-shadow: 0.2rem 0.2rem 0.2rem rgba(0,0,0,0.5);
}

.backLink--wrapper {
    text-decoration: none;
}
.backLink--base {
    display: block;
    margin: 1rem;
    padding: 1rem;
    background: orange;
    border: 0.2rem solid darkorange;
    font-size: 1.2rem;
    font-weight: bold;
    box-shadow: 0.2rem 0.2rem 0.2rem rgba(0,0,0,0.5);
}

.backLink--title {
    @extend .backLink--base;
    margin: 0.5rem;
    background: dodgerblue;
    color: white;
    border: 0.2rem solid darkblue;
}




.scene--title {
    display: flex;
    flex-direction: row;
    border: 0.1rem solid black;
    background: #333333;
    color: #fff;
}

.scene--base {
    background: #007CFF;
    background:
        radial-gradient(
                #1cb7ff,
                #007CFF
        );
    overflow: scroll;
    height: 100%;
}
.scene__home {
    height: 100%;
    overflow: hidden;
    background: #007CFF;
    position: relative;

    @media (orientation: landscape) {
        & {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            text-align: center;
        }
    }
}


.scene__adventure {}

.adventure__stage--list {
    display: flex;
    flex-direction: column-reverse;
}
.stage__box {
    text-decoration: none;
    color: #000;
    display: block;
    border: 0.1rem solid black;
    position: relative;
    margin: 1.5rem 0;
    background: slategrey;
    box-shadow: 0.2rem 0.2rem 0.2rem rgba(0,0,0,0.5);
}
.stage__box--/*${status}*/ {}
.stage__badge {
    position: absolute;
    top: -1rem;
    left: 1rem;
    padding: 0.25rem;
    border: 0.1rem solid black;
    background: #fff;
    box-shadow: 0.2rem 0.2rem 0.2rem rgba(0,0,0,0.5);
}
.stage__badge--new {
    background: lawngreen;
}
.stage__badge--cleared {
    background: lightgoldenrodyellow;
}

.stage__box--title {
    background: #ccc;
    margin: 1rem 0 0;
    padding: 1rem;
}
.stage__box--stages {
    font-size: 0.75rem;
    padding: 0.5rem 1rem;
}

.scene__squad {}
.squad__monitor {

}
.squad__monitor--title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.squad__monitor--listing {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.roster__monitor {}
.roster__monitor--listing {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.unit__card {
    display: flex;
    flex-direction: column;
    border: 0.1rem solid black;
    padding: 0.5rem;
    margin: 0.2rem;
    text-align: center;
    max-width: 5.5rem;
    font-size: 0.8rem;
    background: #fff;
    box-shadow: 0.1rem 0.1rem 0.3rem rgba(0, 0, 0, 0.5);

    button {
        margin-top: 0.5rem;
        padding: 0.2rem;
        font-size: 0.6rem;
        white-space: nowrap;
        text-align: left;

        &.button--add {
            background: palegreen;
        }
        &.button--delete {
            background: indianred;
        }
    }
}

.scene__unit {}
.unit__monitor {}
.unit__monitor--member {
    display: flex;
    flex-direction: row;
    background: #fff;
    padding: 1rem;
    border: 0.1rem solid black;
}
.unit__monitor--img {
    font-size: 4rem;
    text-align: center;
    line-height: 6rem;
    width: 6rem;
    height: 6rem;
    background: lightgoldenrodyellow;
    border: 0.1rem solid black;
    margin-right: 1rem;
}
.unit__monitor--detail {}
.unit__monitor--name {
    font-size: 1.2rem;
    font-weight: bold;
}
.unit__monitor--stats {
    font-size: 0.75rem;
}


.equipment__monitor {}
.equipment__monitor--listing {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    border: 0.1rem solid black;
    padding: 1rem;
    background: #fff;
}

.inventory__monitor {}
.inventory__monitor--listing {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    border: 0.1rem solid black;
    padding: 1rem;
    background: #fff;
}

.item__card {
    display: flex;
    flex-direction: column;
    border: 0.1rem solid black;
    padding: 0.5rem;
    margin: 0.2rem;
    text-align: center;
    max-width: 5.5rem;
    font-size: 0.8rem;
    background: #fff;
    box-shadow: 0.1rem 0.1rem 0.3rem rgba(0, 0, 0, 0.5);

    button {
        margin-top: 0.5rem;
        padding: 0.2rem;
        font-size: 0.6rem;
        white-space: nowrap;
        text-align: left;

        &.button--add {
            background: palegreen;
        }
        &.button--delete {
            background: indianred;
        }
    }
}
.item__card--/*${item.type}*/ {}

.scene__battle--result {}

.result__header {
    text-align: center;
    flex-grow: 1;
    font-weight: 300;

}
.result__header--lost {
    color: red;
    text-shadow: 0.1rem 0.1rem 0.2rem darkred;
    font-family: Didot, Palatino, Times, 'Times New Roman', Georgia, Bookman, 'New Century Schoolbook', 'American Typewriter', "Droid Serif", serif;
    animation: fadein 1s;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}


.rewards--listing {
    margin: 1rem;
    padding: 1rem;
    background: #fff;
    border: 0.1rem solid black;
    display: flex;
    flex-direction: column;
}
.reward__card {
    display: flex;
    flex-direction: row;
    align-content: center;
    margin-bottom: 0.5rem;
}
.reward__card--icon {
    border: 0.1rem solid black;
    box-shadow: 0.2rem 0.2rem 0.2rem rgba(0,0,0,0.5);
    background: #fff;
    width: 3rem;
    height: 3rem;
    line-height: 3rem;
    text-align: center;
    margin-right: 1rem;
}
.reward__card--detail {
    line-height: 3rem;
}

.result__tips {
    font-size: 0.75rem;
    border: 0.2rem solid #fff;
    margin: 1rem;
    padding: 0 0.75rem 0.75rem;
    background: #666;
    color: #fff;
    text-shadow: 0 0 0.1rem #000;
    border-radius: 3px;
    box-shadow:
        0.2rem 0.2rem 0.2rem #000,
        inset 0.1rem 0.1rem 0.5rem #000
    ;
    position: relative;
}
.result__tips--paging {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    font-size: 0.6rem;
    display: flex;
    flex-direction: row;
    align-content: center;
    cursor: pointer;
}
.result__tips--next {
    border: 0.05rem solid rgba(255,255,255,0.25);
    padding: 0 0.5rem;
    margin-right: 0.5rem;
    line-height: 1.5rem;
    height: 1.5rem;

    &:focus,
    &:active {
        margin: 0.05rem 0.45rem 0 0.05rem;
        box-shadow: inset 0.05rem 0.05rem 0.2rem #000;
    }
}
.result__tips--counter {
    line-height: 1.5rem;
}

.scene__battleStage {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;

    @media (orientation: landscape) {
        & {
            flex-direction: row;
        }
    }

    & > * {
        border: 0.1rem solid black;
    }
}
.battleStage__stage {
    background: green;
    border: 0.1rem solid darkgreen;
    height: 50%;
    min-height: 50%;
    flex-grow: 0;

    @media (orientation: landscape) {
        & {
            height: 100%;
            width: 60%;
            min-width: 60%;
        }
    }
}
.battleStage__header {
    background: #000;
    color: #000;
    line-height: 1rem;
    height: 1.5rem;
    padding: 0.25rem 0.5rem;
    border-bottom: 0.1rem solid #fff;
}
.battleStage__header--animating {
    .battleStage__header--title {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        line-height: 2rem;
        padding: 0.5rem 0;
        margin-top: 9rem;
        font-size: 1.5rem;
        border-top: 0.3rem solid #fff;
        border-bottom: 0.3rem solid #fff;
        background: #000;
        text-align: center;
        animation: slideFadeIn 2.7s;
        white-space: nowrap;
    }
}
.battleStage__header--animated {
    color: #fff;
}

@keyframes slideFadeIn {
    0% {
        opacity: 0;
        margin-top: 9rem;
        text-indent: 20rem;
        color: #fff;
    }
    25% {
        opacity: 1;
        margin-top: 9rem;
        text-indent: 0;
        color: #fff;
    }
    75% {
        opacity: 1;
        margin-top: 9rem;
        font-size: 1.5rem;
        text-indent: 0;
        color: #fff;
    }
    100% {
        opacity: 0;
        margin-top: 0;
        font-size: 1rem;
        text-indent: -10rem;
        color: #fff;
    }
}



.battleStage__iab {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-grow: 0;
    background: black;
    color: white;
    align-items: center;

    @media (orientation: landscape) {
        & {
            width: auto;
            height: 100%;
            flex-direction: column;
        }
    }
}
.battleStage__ui {
    flex-grow: 1;
    min-height: 0;
}

.battleStage__monitor {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
}
.battleStage__actors {
    flex-grow: 0;
    width: 50%;
    height: 100%;
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    overflow: scroll;
    justify-content: center;
}
.battleStage__actors--good-guys {
    flex-wrap: wrap-reverse;
}
.battleStage__actors--bad-guys {
    flex-wrap: wrap;
}
.no__units {}

.actor__card {
    width: 4rem;
    min-height: 3rem;
    margin: 0 0.25rem 0.5rem;
    border: 0.1rem solid black;
    background: #fff;
    text-align: center;
    box-shadow: 0.1rem 0.1rem 0.3rem rgba(0, 0, 0, 0.5);

    &.selected {
        background: greenyellow;
    }
}
.actor__card--img {
    text-shadow: 0 0 0.1rem #000;
}
.actor__card--name {
    font-size: 0.75rem;
}
.actor__card--bar {}
.health__bar {
    width: 90%;
    margin: 0 auto;
    display: block;
}

.interactiveActionsBar {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    overflow: hidden;
    padding: 0.2rem;
    margin: 0.3rem auto;
    flex-grow: 1;

    @media (orientation: landscape) {
        & {
            flex-direction: column;
        }
    }
}
.iab__slot {
    border: 0.2rem solid #ccc;
    margin: 0.2rem;
    text-align: center;
    width: 2rem;
    height: 2rem;
    line-height: 2rem;
    display: block;
    flex-shrink: 0;

    &.selected {
        background: greenyellow;
        text-shadow: 0 0 0.3rem #000;
    }
}

.iab__slot--good {
    border-color: green;
}
.iab__slot--bad {
    border-color: red;
}

.turnCounter {
    flex-grow: 0;
    background: #007CFF;
    font-size: 0.75rem;
    height: 2rem;
    line-height: 1.8rem;
    width: 1.8rem;
    min-width: 1.8rem;
    text-align: center;
    border: 0.1rem solid #fff;
    border-radius: 0.2rem;
}

.battleLog {
    color: #fff;
    border: 0.3rem solid black;
    background-color: black;
    background-image: radial-gradient( rgba(0, 150, 0, 0.75), black 120%);
    display: flex;
    flex-direction: column;
    height: 100%;
}
.battleLog__label {
    margin: 0;
    padding: 0.3rem 0.5rem;
    font-size: 0.75rem;
    background: black;
    border-bottom: 0.2rem solid darkgray;
    color: white;
    flex-grow: 0;
}
.battleLog__display {
    overflow-y: scroll;
    flex-grow: 1;
    p {
        font-size: 0.75rem;
        margin: 0.3rem 0.5rem;
    }
}


.storyMode {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.2);
    z-index: 999;
    color: #fff;
}
.storyMode__wrapper {
    margin-top: 2rem;
    border: 0.3rem solid white;
    border-radius: 0.2rem;
    background: #333;
    box-shadow: 0 0 0.4rem rgba(0, 0, 0, 0.5),
    inset 0.4rem 0.4rem 0.4rem rgba(0, 0, 0, 0.5);
    padding: 2.5rem 1rem 4rem;
    position: absolute;
    top: 40%;
    left: 2%;
    right: 2%;
}
.storyMode__actor {
    position: absolute;
    top: -2rem;
    display: flex;
    align-content: center;
    align-items: flex-end;
    justify-content: center;
    flex-wrap: wrap;
}
.storyMode__actor--left {
    left: 0;
    flex-direction: row;
}
.storyMode__actor--right {
    right: 0;
    flex-direction: row-reverse;
}
.storyMode__avatar {
    width: 4rem;
    height: 4rem;
    border: 0.2rem solid black;
    border-radius: 0.2rem;
    background: white;
    margin: 0 1rem;
    text-align: center;
    line-height: 4rem;
    font-size: 2rem;
    text-shadow: 0 0 0.1rem #000;
}
.storyMode__name {
    font-size: 1.5rem;
    text-shadow: 0 0 0.3rem #000;
}
.storyMode__text {}
