/* fredericka-the-great-regular - latin */
@font-face {
    font-family: 'Fredericka the Great';
    font-style: normal;
    font-weight: 400;
    src: local('Fredericka the Great'), local('FrederickatheGreat'),
    url('fonts/fredericka-the-great-v7-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
    url('fonts/fredericka-the-great-v7-latin-regular.woff') format('woff'), /* Modern Browsers */
    url('fonts/fredericka-the-great-v7-latin-regular.ttf') format('truetype'); /* Safari, Android, iOS */
}

